.Home {
    width: 100vw;
    min-height: 100vh;
}

.heroImage {
    width: 100%;
    height: 100vh;
    background: rgb(211, 208, 208);
    background-position: bottom;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
}

.playGround {
    width: 80%;
    max-width: 80%;
    min-height: 100vh;
    margin: 0 auto;
    padding: 12rem 0;
}

.callout {
    text-transform: uppercase;
    letter-spacing: 1px;
    color: grey;
    font-weight: 400;
    font-size: 1.3rem;
}

.discover {
    color: rgb(32, 32, 32);
    font-weight: 600;
    font-size: 3rem;
}

.songContainer {
    margin-top: 4rem;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-gap: 3rem;
}

.songImage {
    width: 50rem;
    height: 50rem;
    border-radius: 50%;
    margin-top: var(--width--primary);
    animation: rotating 20s linear infinite;
}

.modalContainer {
    position: fixed;
    height: 100vh;
    width: 30rem;
    top: 0;
    right: 0;
    background: var(--color--primary);
    transition: all .3s ease;
    display: flex;
    flex-flow: column;
    align-items: center;
    padding-top: 15rem;
    z-index: 20;
    
}

.modalContainer h2 {
    font-size: 2rem;
    color: floralwhite;
    letter-spacing: 1px;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 2rem;
}

.modal-enter {
    transform: translateX(100%);
}

.modal-enter-active {
    transform: translate(0);
    transition: all .3s ease;
}

.modal-exit {
    transform: translate(0);
}

.modal-exit-active {
    transform: translateX(100%);
    transition: all .3s ease;
}

@keyframes rotating {
    from {
        -ms-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -ms-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -webkit-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}